import { eddsa } from 'elliptic';
import { Poseidon, curve_hash, groth16_verify } from './wasm/zk/pkg';

export * from './memory';
export * from './backend';
export * from './instance';
export * from './environment';

global.eddsa = () => global._eddsa || (global._eddsa = new eddsa('ed25519'));
const poseidon = new Poseidon();
global.poseidon_hash = poseidon.hash.bind(poseidon);
global.curve_hash = curve_hash;
global.groth16_verify = groth16_verify;
